  display: -webkit-flex;
  display: flex;
}

.action-text {
  font-size: 0.5rem;
  color: rgb(186, 188, 189);
  padding-top: 2rem;
}

.action-amount {
  font-size: 1.3rem;
  font-weight: bold;
  margin-top: -23px;
  color: rgb(44, 44, 48);
}

.action-btn {
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
  margin-right: 0.6rem;
  border: none;
  color: white;
  font-size: 0.9rem;
  cursor: pointer;
}

.action-btn:hover {
  box-shadow: 0 0 0;
  transition: linear all 2s;
}

.action-btn-left {
  left: 2rem;
  background-image: linear-gradient(
    280deg,
    rgba(254, 151, 194, 0.7),
    rgb(237, 78, 112)
  );
  box-shadow: 5px 9px 11px rgba(237, 78, 112, 0.4);
}

.action-btn-middle {
  left: 2rem;
